<template>
  <div>
    <heads />
    <div class='main'>
      <div class="title">请选择你的身份</div>
      <div class="employer" v-for="(item, index) in list" :key="index" @click="current = index">
        <div class="bg">
          <img :src="require(`@/assets/${item.tag}.png`)" alt="">
        </div>
        <div class="sele" v-if="current === index">
          <img src="@/assets/choice.png" alt="">
        </div>
        <div class="tip">
          <div class="name">{{ item.name }}</div>
          <div class="tips">{{ item.tips }}</div>
        </div>
      </div>
      <van-button class="btn" block round type="info" color="#1F75D9"
        @click="confirm(current ? 'serve' : 'employer')">确认
      </van-button>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      current: 0,
      list: [
        {
          tag: 'employer',
          name: '我是雇主',
          tips: '发需求、找服务'
        }, {
          tag: 'serve',
          name: '我是人才',
          tips: '抢需求、找项目'
        }
      ]
    }
  },
  mounted () {
    this.current = localStorage.getItem('CurrentIdentity') === 'serve' ? 1 : 0
  },
  methods: {
    confirm (tag) {
      localStorage.setItem('CurrentIdentity', tag)
      this.$store.dispatch('ActionsSetUserType', tag)
      this.$router.go(-1)
    }
  }
}
</script>
<style lang='less' scoped>
.main {
  background: linear-gradient(0deg, #DCE2E8 0%, #F0F1F1 100%);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 40px;

  .employer {
    height: 127px;
    width: 100%;
    margin-bottom: 47px;
    border-radius: 10px;
    overflow: hidden;
    position: relative;

    .bg {
      position: absolute;
      width: 100%;
      height: 100%;
    }

    .sele {
      height: 42px;
      width: 66px;
      position: absolute;
      z-index: 2;
      left: 0;
      top: 0;
    }

    .tip {
      display: flex;
      flex-direction: column;
      position: relative;
      z-index: 2;
      margin-top: 47px;
      margin-left: 24px;

      .name {
        font-size: 19px;
        line-height: 1;
        font-weight: 800;
        color: #FFFFFF;
      }

      .tips {
        font-size: 12px;
        line-height: 1;
        font-weight: 400;
        color: #FFFFFF;
        margin-top: 16px;
      }
    }
  }

  .btn {
    margin-top: 16px;
  }

  .title {
    font-size: 24px;
    line-height: 1;
    padding-top: 60px;
    text-align: center;
    font-weight: 800;
    color: #424242;
    margin-bottom: 58px;
  }
}
</style>